.view {
    width: 90%;
    box-shadow: 0 0 4px 5px #000;
    padding: 10px;
    margin: auto;
    margin-top: 20px;
    margin-bottom: 20px;
}

.nav-menu {
    width: 95%;
    margin: auto;
    box-shadow: 0 0 5px 5px #777;
    padding: 20px;
    background-color: #ddd;
}

.nav-menu ul {
    list-style: none;
    display: flex;
}

.nav-menu li {
    width: max-content;
    padding: 10px;
    box-shadow: 0 0 1px 2px #000;
    border-radius: 10px;
    margin-right: 10px;
    color: white;
    background-color: #000;
    font-weight: bold;
}

.nav-menu li:hover {
    cursor: pointer;
    background-color: white;
    color: #000;
}


.main_view {
    box-shadow: 0 0 5px 5px #777;
    margin: auto;
    margin-top: 20px;
    text-align: left;
    padding: 10px;
    font-size: 18px;
    font-weight: bold;
    font-family: 'Times New Roman', Times, serif;
    text-indent: 10px;
    background-color: #eee;
    display: flex;
    height: 60vh;
    width: 95%;
}

.main_view>.mview,
.ssview {
    margin: 20px;
    margin-left: 50px;
    box-shadow: 0 0 1px 4px #555;
    border-radius: 20px;
    padding: 20px;
    height: 80%;
}
.scview{
    overflow-y: scroll;
    height: 60%;
    margin: 20px;
    margin-left: 50px;
    box-shadow: 0 0 1px 4px #555;
    border-radius: 20px;
    padding: 20px;
}

.ssview {
    margin-left: 210px;
}

.main_view .menu_sub,
.main_view .menu_sub_sub {
    list-style: none;

}

.main_view .menu_sub {
    margin-left: -25px;
}

.main_view li {
    cursor: pointer;
    border-radius: 10px;
    width: 100px;
    box-shadow: 0 0 1px 2px #888;
    background-color: #000;
    color: white;
    padding: 10px;
    margin-top: 20px;
    margin-bottom: 20px;
}

.main_view li:hover {
    box-shadow: 0 0 1px 2px #333;
    background-color: #eee;
    color: #000;
}

.menu_sub_sub {
    display: flex;
    margin-left: -40px;
}

.menu_sub_sub>li {
    margin: 2px;
    cursor: pointer;
    font-size: 14px;
    font-weight: bold;
    width: max-content;
    padding: 10px;
}